@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  text-decoration: none;
}

:root {
  --color-green: #1ed760;
  --color-blue: #2941ab;
  --color-gray: #b3b3b3;
  --color-black-first: #121212;
  --color-black-second: #282828;
  --color-black-third: #171717;
}

body {
  background-color: #121212;
  background-color: var(--color-black-first);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.green {
  color: #1ed760;
  color: var(--color-green);
}

.loading {
  height: 60vw;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Mensagem {
  animation: spin 2s linear infinite;
  border: 5px solid gray;
  border-radius: 50%;
  border-top: 5px solid var(--color-green);
  height: 30px;
  width: 30px;
}

@keyframes spin {

  0% { transform: rotate(0deg); }

  100% { transform: rotate(360deg); }
}

.Login {
  align-items: center;
  background-color: var(--color-black-first);
  display: flex;
  flex-direction: column;
  grid-gap: 25px;
  gap: 25px;
  height: 100vh;
  justify-content: center;
  width: 100vw;
}

.form-login {
  align-items: center;
  background-color: white;
  border-radius: 5px;
  display: flex;
  height: 200px;
  justify-content: center;
  max-height: 300px;
  max-width: 400px;
  width: 100%;
}

.form-login form {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  height: 50%;
  justify-content: space-evenly;
  width: 80%;
}

.logo {
  align-items: center;
  color: white;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  justify-content: center;
  max-width: 200px;
  width: 100%;
}

.logo span {
  font-size: 1.6em;
}

input[ type = 'text' ] {
  background-color: #fafafa;
  border: none;
  border-bottom: 1.5px solid black;
  border-radius: 5px;
  font-size: 0.9em;
  font-weight: 600;
  height: 40px;
  margin-top: 5px;
  padding: 12px;
  width: 100%;
}

input:focus {
  outline: none;
}

button {
  background-color: var(--color-green);
  border: none;
  border-radius: 25px;
  font-size: 0.9em;
  font-weight: 600;
  padding: 12px 24px;
}

button:hover:enabled {
  cursor: pointer;
  transform: scale(1.05);
}

button:hover:disabled {
  cursor: not-allowed;
}

.Header {
  align-items: center;
  background-color: black;
  color: white;
  display: flex;
  font-weight: 700;
  height: 80px;
  justify-content: space-around;
  width: 100%;
}

.nav-section {
  align-items: center;
  display: flex;
  font-size: 0.9em;
  grid-gap: 25px;
  gap: 25px;
  justify-content: space-around;
}

.profile-header {
  align-items: center;
  color: var(--color-green);
  display: flex;
  grid-gap: 5px;
  gap: 5px;
}

.profile-header img {
  border-radius: 50%;
  width: 30px;
}

nav {
  display: flex;
  grid-gap: 30px;
  gap: 30px;
  list-style: none;
}

nav li a {
  color: white;
}

nav li a:hover {
  color: var(--color-green);
}

.Header hr {
  height: 15px;
}


.Footer {
  background-color: black;
  bottom: 0;
  color: white;
  font-size: 0.9em;
  font-weight: 600;
  height: 60px;
  left: 0;
  padding-top: 20px;
  position: fixed;
  text-align: center;
  width: 100%;
}

.Search {
  align-items: center;
  background-color: var(--color-black-second);
  box-shadow: rgba(0, 0, 0, 0.2) 0 2px 8px 0;
  display: flex;
  height: 120px;
  justify-content: center;
}

.search-header {
  align-items: center;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  max-width: 450px;
  width: 100%;
}

.search-result {
  font-weight: 600;
  margin: 0 auto;
  margin-bottom: 80px;
  max-width: 80%;
}

.search-title-center {
  color: white;
  font-size: 1.2em;
  font-weight: 700;
  line-height: 75px;
  text-align: center;
}

.album-list {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
}

.album-card {
  background-color: var(--color-black-third);
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0 , rgba(27, 31, 35, 0.15) 0 0 0 1px;
  display: flex;
  flex-direction: column;
  height: 280px;
  padding: 15px;
  transition: background 0.3s ease-out;
  width: 210px;
}

.album-card:hover {
  background-color: var(--color-black-second);
  cursor: pointer;
}

.album-card img {
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.5) 0 6px 24px 0 , rgba(0, 0, 0, 0.5) 0 0 0 1px;
  height: 180px;
  width: 180px;
}

.album-card-info {
  line-height: 25px;
  padding-top: 10px;
  width: 180px;
}

.album-card-info p:nth-of-type( 1 ) {
  color: white;
  font-size: 0.9em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.album-card-info p:nth-of-type( 2 ) {
  color: var(--color-gray);
  font-size: 0.75em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}


.track-container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.MusicCard {
  align-items: center;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  height: 80px;
  justify-content: flex-start;
}

.track-number {
  color: var(--color-gray);
  font-size: 0.9em;
  width: 5%;
}

.track-infos {
  width: 35%;
}

.track-infos p:nth-of-type( 1 ) {
  font-size: 0.9em;
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.track-infos p:nth-of-type( 2 ) {
  color: var(--color-gray);
  font-size: 0.8em;
  font-weight: 600;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

audio {
  height: 40px;
  width: 40%;
}

.MusicCard label {
  display: flex;
  justify-content: flex-end;
  width: 10%;
}

.MusicCard img {
  height: 35px;
  width: 35px;
}

audio::-webkit-media-controls-panel {
  background-color: var(--color-green);
}

audio:hover, audio:focus, audio:active {
  box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.4);
  transform: scale(1.05);
}

input[ type = 'checkbox' ] {
  -webkit-appearance: none;
          appearance: none;
  background-color: var(--color-black-second);
  cursor: pointer;
  height: 10px;
  outline: none;
  position: relative;
  transform: rotate(90deg);
  width: 10px;
}

input[ type = 'checkbox' ]:before {
  background-color: var(--color-black-second);
  border-radius: 75px 75px 0 0;
  content: '';
  height: 60%;
  position: absolute;
  top: -59%;
  width: 100%;
}

input[ type = 'checkbox' ]:after {
  background-color: var(--color-black-second);
  border-radius: 75px 75px 0 0;
  content: '';
  height: 60%;
  position: absolute;
  right: 79%;
  top: 20%;
  transform: rotate(-90deg);
  width: 100%;
}

input[ type = 'checkbox' ]:checked, input:checked[ type = 'checkbox' ]:before, input:checked[ type = 'checkbox' ]:after {
  background-color: var(--color-green);
}

input[ type = 'checkbox' ]:hover {
  opacity: 0.7;
}

.Album {
  color: white;
  margin: 0 auto;
  width: 80%;
}

.album-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.album-infos {
  align-items: flex-end;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  height: 280px;
  justify-content: flex-start;
  width: 80%;
}

.album-infos small {
  font-size: 0.8em;
}

.album-infos p:nth-of-type( 1 ) {
  font-size: 2em;
}

.album-infos span {
  color: var(--color-gray);
  font-weight: 600;
}

.album-infos img {
  border-radius: 6px;
  height: 220px;
  width: 220px;
}

.album-tracks {
  display: flex;
  flex-direction: column;
  margin-bottom: 80px;
  width: 80%;
}

.album-content hr {
  border: 1px solid var(--color-gray);
  opacity: 0.1;
  width: 80%;
}

.Favorites {
  color: white;
  margin: 0 auto;
  width: 80%;
}

.favorites-content {
  align-items: center;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.favorites-infos {
  align-items: flex-end;
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  height: 280px;
  justify-content: flex-start;
  width: 80%;
}

.favorites-infos small {
  font-size: 0.8em;
}

.favorites-infos p:nth-of-type( 1 ) {
  font-size: 2em;
}

.favorites-infos span {
  color: var(--color-gray);
  font-weight: 600;
}

.favorites-infos img {
  border-radius: 6px;
  height: 220px;
  width: 220px;
}

.favorites-tracks {
  display: flex;
  flex-direction: column;
  margin-bottom: 80px;
  width: 80%;
}

.favorites-content hr {
  border: 1px solid var(--color-gray);
  opacity: 0.1;
  width: 80%;
}

.favorites-infos-text {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.favorites-infos-text img {
  border-radius: 50%;
  height: 25px;
  width: 25px;
}

.favorites-profile {
  align-items: center;
  display: flex;
  grid-gap: 5px;
  gap: 5px;
}

.blurred {
  filter: blur(5rem);
  position: absolute;
  z-index: -1;
}

.cover {
  filter: drop-shadow(2px 2px 5px rgb(0 0 0 / 0.5));
  position: relative;
  z-index: 0;
}

.Profile {
  align-items: center;
  background-color: var(--color-black-second);
  color: white;
  display: flex;
  grid-gap: 50px;
  gap: 50px;
  height: 280px;
  justify-content: center;
  margin: 0 auto;
  width: 100%;
}

.profile-infos {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.profile-infos p:nth-of-type( 1 ) {
  font-size: 2.2em;
}

.profile-infos p:nth-of-type( n+2 ) {
  color: var(--color-gray);
  font-weight: 600;
}

.Profile img {
  border-radius: 50%;
  height: 220px;
  width: 220px;
}

.Profile a {
  color: var(--color-black-first);
}

.Profile button {
  width: 150px;
}

.ProfileEdit {
  background-color: var(--color-black-second);
  border-radius: 6px;
  margin: 60px auto;
  max-width: 500px;
  padding: 30px;
}

.ProfileEdit form {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.ProfileEdit img {
  align-self: center;
  border-radius: 50%;
  height: 220px;
  width: 220px;
}

.ProfileEdit label {
  color: white;
}

.ProfileEdit button {
  align-self: center;
  width: 150px;
}

textarea {
  border: none;
  border-radius: 5px;
  font-size: 0.9em;
  font-weight: 600;
  height: 60px;
  margin-top: 5px;
  padding: 12px;
  resize: none;
  width: 100%;
}

textarea:focus {
  outline: none;
}

